@import url(https://fonts.googleapis.com/css?family=Chilanka&display=swap);
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase; }

html, body, .root {
  height: 100%; }

body {
  line-height: 1.5; }

body,
.app {
  background-color: #ececec;
  color: #adadad; }

a {
  color: #8cdba0;
  text-decoration: none;
  font-weight: 700; }

/* main page */
.list-header-center {
  padding: 5px 0;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%;
  max-width: 48em; }
  @media (min-width: 64em) {
    .list-header-center {
      flex-direction: row; } }

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%;
  max-width: 48em; }

.list-books-title {
  width: 90%;
  align-self: center;
  font-family: 'Chilanka', cursive;
  font-size: 1.5em;
  font-weight: 700;
  text-align: center;
  color: #8cdba0; }
  @media (min-width: 64em) {
    .list-books-title {
      width: 40%; } }

.list-books-content {
  align-self: center;
  padding: 80px 0 80px;
  flex: 1 1; }

.list-header {
  background-color: #ececec;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 100%; }

.menu {
  display: flex;
  align-self: center;
  justify-content: center;
  align-content: center;
  width: 65%;
  font-size: .8em; }
  @media (min-width: 64em) {
    .menu {
      width: 30%; } }

.repo {
  position: fixed;
  font-size: .8em;
  top: 0;
  right: 0;
  font-weight: 700;
  padding: 10px; }

.menu img {
  width: 55px;
  padding: 0 15px; }

.menu img:hover {
  opacity: .5; }

.bookshelf {
  padding: 0 10px 20px; }

.bookshelf-title {
  width: 100%;
  align-self: center;
  font-family: 'Chilanka', cursive;
  font-size: 1.3em;
  font-weight: 700;
  color: #8cdba0;
  margin-bottom: 70px; }

.bookshelf-books {
  text-align: center;
  padding: 10px 0 0; }

.open-search-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin-left: 25px; }

.open-search {
  align-self: center;
  padding-top: 6px;
  font-size: 1.4em;
  z-index: 99;
  color: white;
  text-decoration: none;
  font-family: 'Chilanka', cursive; }

.open-search:hover {
  text-decoration: underline; }

.open-search-icon {
  width: 15px;
  height: 15px; }

.search-books-bar {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  background-color: #ececec; }

/* search page */
.search-books-bar input {
  padding: 5px;
  width: 35%;
  border: none; }

.searchTermSection {
  display: flex;
  width: 90%;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: auto; }
  @media (min-width: 64em) {
    .searchTermSection {
      width: 80%; } }

.searchTermSectionDesc {
  padding: 10px 0; }

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background-image: url(/book-tracker-9000/static/media/arrow-back.20e8847d.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
  border: none;
  outline: none; }

button:active {
  border: red;
  outline: none; }

.search-books-results {
  padding: 60px 0 0 0; }

/* books grid */
.books-grid {
  list-style-type: none;
  padding: 10px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.books-grid li {
  padding: 0 7px; }

.book {
  width: 180px;
  padding-bottom: 90px; }

.book-top {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end; }

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #60ac5d;
  background-image: url(/book-tracker-9000/static/media/arrow-drop-down.9d4e3f36.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer; }

/* book cover */
.book-cover {
  width: 100%;
  height: 265px;
  box-shadow: inset 3px 3px 3px rgba(255, 255, 255, 0.4), inset -3px -3px 3px rgba(0, 0, 0, 0.4);
  background: #eee; }

.book-cover-line {
  margin-left: 10px;
  width: 3px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2); }

.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em; }

